<template>
  <div>
    <el-row style="margin-bottom:20px;">
      <el-card shadow="always">
        <span class="content-title">组件管理</span>
        <el-button
          type="success"
          @click="openAddDialog()"
        >新增</el-button>
      </el-card>
    </el-row>
    <treeTable 
      :columns="columns" 
      :tree-structure="true" 
      :data="dataSource"
      @on-delete="handleDelete"
      @on-edit="handleEdit"
      @on-add="handleAdd"
    >
    </treeTable>
    <el-dialog
      :title="2 == dialog_type ? '编辑': '新增'"
      :visible.sync="dialogVisible"
      width="60%"
      top="5vh"
      :append-to-body='true'
      :before-close="handleClose"
      :closeOnClickModal='false'
    >
      <div class="el-dialog-news">
        <el-form
          label-position="top"
          :rules="rules"
          ref="formLabelAlign"
          label-width="80px"
          :model="formLabelAlign"
        >
          <el-form-item
            label="菜单名称"
            prop="title"
          >
            <el-input v-model="formLabelAlign.title"></el-input>
          </el-form-item>
          <el-form-item label="链接地址">
            <el-input v-model="formLabelAlign.link"></el-input>
          </el-form-item>
          <el-form-item label="发布状态" prop="status">
            <el-radio-group v-model="formLabelAlign.status">
              <el-radio :label="0">未发布</el-radio>
              <el-radio :label="1">发布</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label="发布时间"
            prop="release_time"
          >
            <el-date-picker
              v-model="formLabelAlign.release_time"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss"
              placeholder="选择日期"
              default-time="01:01:01"
            >
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="handleClose">取 消</el-button>
        <el-button
          type="primary"
          @click="addInfo('formLabelAlign')"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import {TreeGrid} from '@/components/treeTable'
import { authName } from "@/config";
import { storageHelper, getCurTime, utcTimeChange } from "@/utils";
import { prefix } from '@/api';
export default {
  data() {
    return {
      auth_name: "",
      selectClass: "el-icon-arrow-down",
      content_show: false,
      forms: {
        parent_id: 0,
        title:''
      },
      //表格
      dataSource: [],
      currentPage: 1,
      page_size: 15,
      total: 0,
      //弹出框
      dialogVisible: false,
      //表单
      formLabelAlign: {
        parent_id:0,
        title: "",
        status: 0,
        release_time: "",
        link: ""
      },
      rules: {
        parent_id: [{ required: true, message: "请输入活动名称", trigger: "blur" }],
        status: [{ required: true}],
        release_time: [{ required: true }]
      },
      token:'',
      //下拉选项分页
      second_select_list:[],
      page_select_size:10,
      currentSelectPage:1,
      select_total:15,

      columns: [
        {
          text: "菜单名称",
          value: "title",
        },
        {
          text: "状态",
          value: "status",
          width: 100
        },
        {
          text: "链接地址",
          value: "link",
          width: 200
        },
      ],
      component_type_list: [],
      dialog_type: 0,        //0-添加根节点,1-添加子节点,2-修改节点
      loading: null,
    };
  },
  mounted() {
    this.auth_name = storageHelper.getItem(authName, "healthcare");
    this.getInfoList("1", "15");
  },
  methods: {
    //打开添加
    openAddDialog() {
      this.dialog_type = 0;
      this.formLabelAlign = {
        parent_id:0,
        title: "",
        status: 1,
        link: ""
      };
      this.dialogVisible = true;
    },
    handleClose() {
      var that = this;
      this.$confirm('是否保存？',{
        showClose:false
      })
      .then(() => {
        that.addInfo('formLabelAlign');
      })
      .catch(() => {
        this.dialogVisible = false
      });
    },
    //表格删除按钮
    handleDelete(index,row) {
      var that = this;
      this.$confirm("是否确认删除该条信息?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        var params = { nav_id: row.nav_id };
        that.submitInfo("content/nav/delete", params, "删除成功!");
      }).catch(() => {});
    },
    //表格编辑按钮
    handleEdit(index,row) {
      this.dialog_type = 2;
      this.formLabelAlign = {
        nav_id:row.nav_id,
        id:row.id,
        parent_id:row.parent_id,
        title: row.title,
        status: row.status,
        release_time: utcTimeChange(row.release_time),
        link: row.link
      };
      this.dialogVisible = true;
    },
    showLoading(){
      this.loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
    },
    closeLoading(){
      if(this.loading){
        this.loading.close();
      }
    },
    //表格添加按钮
    handleAdd(index,obj){
      this.dialog_type = 1;
      this.formLabelAlign = {
        parent_id:obj.nav_id,
        title: "",
        status: 1,
        release_time: "",
        link: ""
      };
      this.dialogVisible = true;
    },
    //编辑框提交按钮
    addInfo(formType) {
      var that = this;
      this.showLoading();
      this.$refs[formType].validate(valid => {
        if (valid) {
          var method = "";
          var info = "";
          if (that.formLabelAlign.hasOwnProperty("nav_id")) {
            method = "content/nav/update";
            info = "修改成功!";
          } else {
            method = "content/nav/create";
            info = "添加成功!";
          }
          that.submitInfo(method, that.formLabelAlign, info);
        } else {
          that.closeLoading();
          return false;
        }
      });
    },
    submitInfo(method, params, info) {
      var that = this;
      axios.post(method, params).then(
        function(response) {
          that.closeLoading();
          var code = response.code;
          if (0 == code) {
            that.$message({
              type: "success",
              message: info
            });
            that.dialogVisible = false;
            that.getInfoList(that.currentPage, that.page_size);
          } else {
            that.$message({
              type: "success",
              message: "操作失败:" + response.message
            });
          }
        },
        function(error) {
          that.closeLoading();
        }
      );
    },
    getInfoList(page_index, page_size){
      var params = {};
      var that = this;
      axios.get("content/nav/treeListAll", { params }).then(
        function(res) {
          var code = res.code;
          if (0 == code) {
            var array = [];
            var rdata = res.data;
            var json = rdata;
            for (var i = 0; i < json.length; i++) {
              array.push(json[i]);
            }
            that.dataSource = array;
          } else {
            //console.log(code);
          }
        },
        function(error) {
          //console.log(error);
        }
      );
    },
  },
};
</script>

<style scoped>
.content-title {
  margin-right: 20px;
}
.advanced-box {
  padding: 20px 10px;
  margin-top: 15px;
  background-color: #f7f7f7;
}
.pager {
  float: right;
  padding: 10px 0;
}
.el-dialog__header {
  border-bottom: 1px solid #ccc;
}
.el-dialog-news {
  height: 530px !important;
  padding-right: 15px;
  overflow-y: scroll;
}
.el-dialog__footer {
  border-top: 1px solid #ccc;
}
.el-form-item__label {
  padding-bottom: 0 !important;
}
</style>


